<!--/-->
<style lang="scss" rel="stylesheet/scss">
    .gov-operate-container {
        background: #fff;
        padding: 20px 300px 20px 100px;
        overflow: auto;
        .addForm {
            .tip {
                text-align: center;
                border-top: 1px solid #ddd;
                color: #ff0000;
                line-height: 50px;
            }
        }
        .dialog-footer {
            text-align: center;
        }
    }
</style>
<template>
    <article class="gov-operate-container">
        <section>
            <el-form class="addForm" :model="form" :rules="rules" ref="form" :label-width="formLabelWidth">
                <el-form-item  label="">
                    <Section-category-menu :placeholder="form.name" :autoClear="true" v-model="form.chapter_id" :reqFun="reqFun"></Section-category-menu>
                </el-form-item>
                <el-form-item prop="course_name" label="" :label-width="formLabelWidth">
                    <el-input v-model="form.course_name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item v-if="!form.ref_id" prop="url" label="" :label-width="formLabelWidth">
                    <el-input v-model="form.url" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="image" label="" :label-width="formLabelWidth">
                    <!---->
                    <div class="img-wrap">
                        <div v-if="form.image" class="image">
                            <img :src="form.image | fillImgPath" alt="" />
                        </div>
                        <div class="button-wrap">
                            <el-button type="primary" @click="() => {$refs.imgcropper.chooseImg()}"></el-button>
                        </div>
                    </div>
                </el-form-item>
                <el-form-item prop="description" label="" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="3" v-model="form.description" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="addate" label="" :label-width="formLabelWidth">
                    <el-date-picker v-model="form.addate" type="date" />
                </el-form-item>
                <el-form-item prop="sort" label="" :label-width="formLabelWidth">
                    <el-input v-model="form.sort" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submit('form')"></el-button>
            </div>
        </section>
        <ImagEcropperInput :compress="1" :isShowBtn="false" ref="imgcropper" :confirmFn="handleImgUploaded" :aspectRatio="ratio"></ImagEcropperInput>
    </article>
</template>
<script>
    import examService from '../../../services/exam/examService'
    import Region from '../../component/select/Region.vue'
    import SectionCategoryMenu from '../../component/select/SectionCategoryMenu.vue'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInputSec.vue'
    function clearFormFn() {
            return  {
                    category_id: void 0,
                    chapter_id: void 0,
                    course_name: '',
                    image: '',
                    tags:'',
                    description: '',
                    material_id:void 0,
                    status:void 0,
                    deleted:void 0,
                    experts_id:void 0,
                    sort:void 0,
                    audited:void 0,
                }
    }
    let _this
    export default {
        components: {
            Region,SectionCategoryMenu,ImagEcropperInput
        },
        data () {
            let validateEmail = (rule, value, callback) => {
                if (!(value || '').match(/^\w+([-+.]\w+)*@\w+([-+.]\w+)*.\w+([-+.]\w+)*$/)) {
                    callback(new Error(''))
                }
                callback()
            }
            let validateMobile = (rule, value, callback) => {
                if (!(value || '').match(/^1[34578]\d{9}$/)) {
                    callback(new Error(''))
                }
                callback()
            }
            return {
                ratio: 0, // 
                govTypes: [ // 
                    {
                        name: '',
                        id: 2
                    },
                    // {
                    //     name: '',
                    //     id: 1
                    // }
                ],
                formLabelWidth: '120px',
                form: clearFormFn(),
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    concact: [
                        {message: '', trigger: 'blur'}
                    ],
                    mobile: [
                        {message: '', trigger: 'blur'},
                        // {validator: validateMobile, trigger: 'blur'}
                    ],
                    email: [
                        {message: '', trigger: 'blur'},
                        // {validator: validateEmail, trigger: 'blur'}
                    ]
                },
                reqFun:()=>{
                    return examService.fetchChapterCategory({
                        // pid: 0,
                        // level: -1,
                        // pagesize:-1
                    })
                }
            }
        },
        computed: {
            govID () {  //govid
                return this.$route.params.id
            }
        },
        activated () {
            _this = this
            xmview.setContentLoading(false)
            if (this.govID == undefined) {
                this.form = clearFormFn()
                return false
            }
            examService.getGovInfo(this.govID).then((ret) => {
                this.form = ret
                this.govID = this.govID
            })
        },
        methods: {
            // 
            handleImgUploaded(data, ext) {
                courseService.commonUploadImageBaseSection({
                    // section_id: this.section.currentID,
                    alias: Date.now() + ext,
                    image: data
                }).then((ret) => {
                    this.form.image = ret.url
                })
            },
            submit (form) { // 
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        this.form = Object.assign(this.form, this.sign)
                        let reqFn, msg 
                        if (this.govID) {
                            this.form.gov_id = this.govID
                            reqFn = examService.updateGov
                            msg = ''
                        }else{
                            reqFn = examService.addGov
                            msg= ''
                        }
                        console.log(this.$route.params.govinfo)
                        if(this.$route.params.govinfo!=undefined){
                            this.form.pid = this.$route.params.govinfo.pid
                        }else{
                            this.form.pid = this.form.village_id||this.form.town_id|| this.form.area_id||this.form.city_id ||this.form.province_id
                        }
                        console.log(this.form)
                        reqFn(this.form).then(() => {
                            xmview.showTip('success', msg)
                        }).then(() => {
                            this.$router.push({name: 'exam-course-manage'})
                        }).catch((ret) => {
                            xmview.showTip('error', ret.message)
                        })
                    } else {
                        return false
                    }
                })
            }
        }
    }
</script>



